.weekly {
  position: relative;
  height: auto;

  @media (min-width: 900px) {
    height: calc(var(--mobile-100vh-fix) - var(--nav-height));
  }

  .main-display {
    .weekly-list {
      width: 100%;
      height: calc(100vh - (var(--nav-height-sm) + var(--header-height)));
      overflow-y: scroll;
      padding: 0 4.5px; /* to prevent box-shadow cut off on the left/right edge due to position absolute */

      /* make horizontal scroll bar transparent */
      &::-webkit-scrollbar {
        display: none;
      }

      @media (min-width: 750px) {
        height: calc(100vh - (var(--nav-height-bg) + var(--header-height)));
        flex-basis: 25%;
      }

      @media (min-width: 900px) {
        display: flex;
        position: absolute;
        overflow-x: scroll;
        width: calc(75% - 50px);
        scrollbar-width: none; /* fix for firefox */
      }

      .list {
        h2 {
          font-size: 1em;
          margin-bottom: 2.3rem;

          @media (min-width: 900px) {
            font-size: 1.5em;
            margin-bottom: 3rem;
          }

          span {
            font-weight: lighter;
            font-size: 1rem;
            color: rgba(0, 0, 0, 0.171);
            margin-left: 0.4rem;
          }

          .date-ordinal {
            margin-left: 0;
          }
        }
      }

      section {
        height: inherit;
        margin-left: -20px;

        div {
          width: 10px;
          height: 100%;
        }
      }
    }
  }
}
